/**
 * Breakpoint Configuration
 * @see https://github.com/Team-Sass/breakpoint/wiki
 *
 */
.main {
  margin: 0 auto;
  padding: 0 3%;
  max-width: 73.6em;
  display: block;
}

.main::after {
  clear: both;
  content: "";
  display: table;
}

