}

.pl .sg-colors,
.sg-main .sg-colors {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}

.pl .sg-colors li,
.sg-main .sg-colors li {
  flex: auto;
  padding: 0.3em;
  margin: 0 0.3em 1em;
